body {
    height: 100vh;
    margin: 0;
    background-image: url(/back.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.container ul {
    list-style: none;
    width: 600px;
    margin: 0;
    padding: 10px;
    margin-top: 2em;
    background-color: antiquewhite;
    border-radius: 15px;
    border: 10px solid white;
}

.container li {
    display: inline-block;
    width: 150px;
    position: relative;
}

.tile {
    display: block;
    width: 120px;
    height: 160px;
    margin: 10px auto;
    transition: .3s;
    opacity: 1;
}

.coinc {
    opacity: 0;
}

.closed {
    background-color: gray !important;
}

.cont-buttons button{
    background-color: darkgoldenrod;
    padding: 10px;
    margin: 10px;
    color: antiquewhite;
    font-size: 24px;
    border-radius: 50%;
    border: 1px solid white;
    width: 50px;
    height: 50px;
}

.cont-buttons button:hover {
    background-color: rgb(117, 86, 6);
}

